<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process172"></a>

<p><br></p>
<p><span class="rvts17">爱要付出代价（</span><span class="rvts50">Love Has A Price</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:321,Y:163</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：洛夫瑞克（</span><span class="rvts20">Lovrik</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">进入贼窝，揭示骗局</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1177.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">该任务可以在浮木城的酒店里接到。通过对话，并尝试与他深入交谈（需要掏钱，掏钱，掏钱），他便会给你酒吧上层的</span><span class="rvts20"></span><span class="rvts21">特殊套房</span><span class="rvts20"></span><span class="rvts21">钥匙。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1178.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">通过和一个蜥蜴族人过夜之后，第二天发现这就是个骗局。此时全身无任何装备，而其它同伴都在楼下。</span>
<br><span class="rvts21">可以选择与这帮犯罪团伙打斗或选择默认买单（其实也不算买单，之后这些装备还会找回来，丢失的物品在酒馆下层黑人头领洛哈那里的一个桌子上）。</span>
<br><span class="rvts21">如果选择打斗的话，建议其它队友火速赶往楼上支援。另外，被骗的角色也应该迅速找到自己的装备（装备就在床头边的背包里）。</span>
<br><span class="rvts21">另外，这里选择过夜的角色是伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹，这些强盗们会被吓跑（因为伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹和浮木城的大多数人都是老相识）。如果选择过夜的角色是腥红王子，那么楼上这位蜥蜴女人将会显现她的蜥蜴公主身份，并与王子一起战斗。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1179.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在所有事情解决后，下楼找到洛夫瑞克，让他为此事付出代价。此时的洛夫瑞克会以干这件不光彩的事是为了养家糊口为由（他说他有个女儿叫做</span><span class="rvts20">Livy</span><span class="rvts21">），乞求放过他。</span>
<br><span class="rvts21">但是如果此时使用另一个角色可以在酒吧厨子那里打听到消息，会发现这个洛夫瑞克根本就没有女儿，就是在满口胡言。你可以选择杀了他，获得额外的经验。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



